import { ContractAbi } from 'web3'
import { BlockNumber, BNFixed } from '../../core'
import { Token } from '../../tokens'
import { Web3CallData } from '../../web3-client'
import {
  Vault,
  VaultContractData,
  VaultContractModel,
  VaultContractOptions,
  VaultNonPayableMethodOptions,
  VaultNonPayableMethodType,
  VaultPayableMethodOptions,
  VaultPayableMethodType,
} from '../vault.model'
import { VaultContract } from './vault-contract.class'
import { ERC20_ABI } from '../vault.const'

export class VaultParetoToken
  extends VaultContract
  implements VaultContractModel
{
  constructor(vault: Vault, token: Token, options?: VaultContractOptions) {
    super(vault, token, options)
  }

  /**
   * Get contract data
   * @returns the blockchain contract data
   */
  public async getContractData(
    blockNumber: BlockNumber = 'latest'
  ): Promise<VaultContractData> {
    const callData = this.makeCallData(blockNumber)
    return this.getData(callData, blockNumber)
  }

  /**
   * Prepare call data
   * @returns the web3 call data
   */
  protected makeCallData(blockNumber?: BlockNumber): Web3CallData[] {
    // Parse vault contract methods
    const { abi, address, protocol, contractType } = this.vault
    const callData = this.makeProtocolData(
      { abi, address, protocol },
      contractType
    )

    return callData
  }

  /**
   * Get vault payable method
   * @param type the payment method type
   * @param options the payment method options
   * @returns the web3 payable method
   */
  public getPayableMethod(type: VaultPayableMethodType, options?: any) {
    try {
      switch (type) {
        default:
          throw new Error('Method not available for this kind of vault')
      }
    } catch (error) {
      console.error(`Contract get method error`, type, error)
      return void 0
    }
  }

  /**
   * Get vault non payable method
   * @param type
   * @param params
   */
  public getValue(
    type: VaultNonPayableMethodType,
    options?: VaultNonPayableMethodOptions
  ): Promise<any> {
    try {
      switch (type) {
        default:
          throw new Error('Value not available for this kind of vault')
      }
    } catch (error) {
      console.error(`Contract get value error`, type, error)
      return Promise.resolve(null)
    }
  }

  /**
   * Mint new USP tokens providing stablecoin as collateral
   * @param options the method params
   * @returns the payable method for stop the epoch
   */
  public mint(options?: VaultPayableMethodOptions) {
    if (
      this.vault.contractType !== 'PARETO_TOKEN' ||
      !this.vault.paretoDollar
    ) {
      throw Error('Wrong vault type')
    }

    if (
      options?.collateralAddress === undefined ||
      options.amount === undefined
    ) {
      throw Error('Collateral address and amount are mandatory')
    }

    const { abi, address } = this.vault
    const { collateralAddress, amount } = options

    // Check deposit amount
    const amountCheck = this.checkContractAmount(amount)
    if (!amountCheck) {
      return
    }

    const params = [collateralAddress, amount]

    return this.getContractPayableMethod({
      abi,
      address,
      method: 'mint',
      params,
    })
  }

  /**
   * Check if wallet is allowed
   * @param options - the method options
   * @returns true if wallet is allowed
   */
  public isWalletAllowed(
    options?: VaultNonPayableMethodOptions
  ): Promise<boolean> {
    try {
      if (
        this.vault.contractType !== 'PARETO_TOKEN' ||
        !this.vault.paretoDollar
      ) {
        throw Error('Wrong vault type')
      }

      if (options?.walletAddress === undefined) {
        throw Error('Wallet address is mandatory')
      }

      const { abi, address } = this.vault
      const method = this.getContractNonPayableMethod({
        abi,
        address,
        method: 'isWalletAllowed',
        params: [options.walletAddress],
      })

      if (!method) {
        throw Error('Not method available')
      }

      return method.call<boolean>()
    } catch (error) {
      return Promise.resolve(false)
    }
  }
}
